.content2{
    background-color: #424242;
    color: aliceblue;
    height: 500px;
    width: 100%;
  }
  .content2 div{
    margin: 60px 80px;
  }
  .content2 div table tr td{
    
    display: inline-block;
    width: 300px;
    margin: 3px 0px;
    font-size: 1.2em;
  }
  .content2 div table tbody tr td{
    text-indent: 15px;
  }
  .content2 div table thead tr td{
   font-size: 2.2em;
   margin-bottom: 30px;
  
  }
  .content2 div table thead tr a{
    color: #fff;
  }
  
  .content2 div table tfoot tr td{
    margin-top: 30px;
    font-size: 1.8em;
    font-weight: bold;
  }