.content{
    width: 1200px;
    margin-top: 65px;
   
    
   padding: 30px 70px;
  
}
.content .conOne{
   
   height: 570px;
}
.content .conOne .left{
    padding-top: 70px;
    padding-left: 50px;
    
    
}
.content .conOne .left h1{
    color: black;
    font-size: 2.9em;
    margin-top: 0px;
    font-weight: lighter;
   
    
}
.content .conOne .left button{
    margin-top: 20px;
    margin-left: 60px;
    background-color: black;
    color: rgb(151, 142, 142);
    padding: 18px 33px;
    font-size: 2em;
    font-weight: bold;
    border: 3px solid #fff;
    border-radius: 45px;
}
.content .conOne .left button:hover{
    background-color: #fff;
    border: 3px solid black;
}
.content .conOne .left{
    width: 550px;
    float: left;
    

}
.content .conOne .left2{
    width: 500px;
    float: left;
   margin-right: 70px;
}

.content .contwo{
    width: 1200px;
    padding-top: 50px;
    height: 470px;
    
}
.content .contwo h1{
    text-align: center;
    color: black;
    font-size: 4.5em;
}
.content .contwo .bottom {
    padding-top: 60px;
    width: 100%;
}
.content .contwo .bottom div{
    width: 275px;
    padding: 0px 10px;
    float: left;
   
}
.content .contwo .bottom div h1{
    font-size: 1.8em;
    font-weight: bolder;
    color: black;
    text-align:center ;
    padding-bottom: 40px;
    line-height: 1.4em;
}
.content .contwo .bottom div p{
    font-size: 1.1em;
    color: black;
    text-align:justify ;
    letter-spacing: 1px;
    line-height: 1.3em;
}
.content .conthree{
    width: 100%;
    height: 950px;
   
    
}
.content .conthree .img{
    height: 400px;
    padding-top: 10px;


}
.content .conthree .img h1{
    padding-top: 130px;
    text-align: center;
    color: black;
    font-size: 4em;
    font-weight: bolder;
    padding-bottom: 80px;
}
.content .conthree .img div{
    float: left;
    width: 170px;
    padding: 0 15px;

}
.content .conthree .img div:nth-of-type(4){
    float: left;
    width: 80.4px;
    padding: 0 55px;

}
.content .conthree .img div img{
    width: 150px;
    height: 50px;
}
.content .conthree .img div img.huawei{
    width: 70px;
    height: 50px;
    
}
.content .conthree .content1{
    
    height: 240px;
}
.content .conthree .content1 div{
    width: 28%;
    float: left;

}


.content .conthree .content1 div:nth-child(n+2){
    margin-left: 80px;
}
.content .conthree .content1 div .box{
    width: 100%;
    padding: 0;
    margin: 0;
}
.content .conthree .content1 div div img{
    float: left;
   width: 160px;
   height: 70px;
}
.content .conthree .content1 h3{
    font-size: 1.3em;
    color: black;
    font-weight: bold;
    text-align: center;
    letter-spacing: 1.6px;
   
}
.content .conthree .content1 p{
  font-size: 1em;
  color: black;
  padding: 10px 0px;
  margin-top: 60px;

}
.content .confour{
    height: 200px;
}
.content .confour p{
    font-size: 2em;
    font-weight: bold;
    color: black;
}