


.bander{
    position: fixed;
    height: 70px;
    
    border: 2px solid black;
    background-color: aqua;
    /* overflow: hidden; */
    min-width: 1350px;
  }
  .bander  img{
    width: 10%;
    height: 35%;
    position: relative;
    top: 25px;
    left: 20px;
  }
  .bander .ulList{
    position: relative;
    top: -10px;
    left: 200px;
  
    
   height: 100%;
  }
  .bander div ul{
    list-style: none;
    width: 100%;
    
  }
  .bander div ul li{
    float: left;
    
    height: 100%;
    font-size: 1.5em;
    
   
   margin: 0 20px;
  
  }
  .bander div ul li:hover{
    color: aliceblue;
  }
  .bander .btnList {
    position: absolute;
    top: 0px;
    right: 50px;
    height: 100%;
    width: 40%;
    
  }
  .bander .btnList button{
    margin: 10px 10px;
    width: 20%;
    height: 70%;
    background-color: black;
    color: wheat;
    border-radius: 25px;
    float: left;
    font-size: 2em;
  }
  .bander .search {
    position: absolute;
    top: 10px;
    right: -80px;
    height: 100%;
   
    
  }
  .bander .search input[type='text']{
    width: 50%;
    padding: 5px ;
    height: 70%;
    text-indent: 20px;
    border-radius: 25px;
    background-color: black;
    color: wheat;
    font-size: 2em;
  }